<template>
    <div class="container">
        <div class="data">
            <div class="data__item" v-for="car in cars" :key="car.id">
                <img :src="car.img" alt="">
                <router-link :to="`cars/${car.id}`">{{ car.name }}</router-link>
                <p>{{ car.text }}</p>
            </div>
        </div>
    </div>
</template>

<script>
import { items } from '@/data'
    export default {
        data() {
            return {
                cars: items
            }
        },
    }
</script>

